@mixin isSelected($color:#000) {
  background-color: #f6f6f7;
  font-weight: 600;
  border-radius: 10px;
  color: $color;
}

div.isactived {
  @include isSelected(var(--mc-menu-active))
}

.MenuItem {
  display: flex;
  align-items: center;
  margin: 10px 0;
  color: var(--mc-menu-text-color);
  cursor: pointer;
  font-size: 16px;
  padding: 10px 5px;

  &:hover {
    transition: all .4s linear;
    @include isSelected(#000);
  }
}

.Icon {
  width: 22px;
  margin-right: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}